"use client";

import Image from "next/image";

interface CertificateCardProps {
  title: string;
  subtitle: string;
  status: "locked" | "completed";
  image: string;
  lockedImage: string;
  description_locked: string;
  description_unlocked: string;
}

export default function CertificateCard({
  title,
  subtitle,
  status,
  image,
  lockedImage,
  description_locked,
  description_unlocked,
}: CertificateCardProps) {
  const isLocked = status === "locked";

  return (
    <div
      className={`border rounded-lg p-6 shadow-md transition ${
        isLocked ? "opacity-50 bg-gray-100" : "bg-white"
      }`}
    >
      {/* üîí Icono de bloqueo */}
      <div className="flex justify-between">
        <h2 className="text-lg font-bold">{title}</h2>
        <Image
          src={isLocked ? lockedImage : "/assets/icons/unlock.svg"}
          alt="Estado"
          width={20}
          height={20}
        />
      </div>

      <p className="text-sm text-gray-500">{subtitle}</p>

      {/* Imagen o preview del certificado */}
      <div className="my-4">
        {isLocked ? (
          <Image
            src={lockedImage}
            alt="Certificado bloqueado"
            width={150}
            height={100}
            className="mx-auto"
          />
        ) : (
          <Image
            src={image}
            alt="Vista previa del certificado"
            width={250}
            height={180}
            className="mx-auto"
          />
        )}
      </div>

      {/* Texto descriptivo */}
      <p className="text-center text-gray-600 whitespace-pre-line">
        {isLocked ? description_locked : description_unlocked}
      </p>

      {/* üèÜ Bot√≥n de Descargar y Compartir solo si est√° desbloqueado */}
      {!isLocked && (
        <div className="mt-4 text-center">
          <button className="px-4 py-2 border rounded-lg text-primary border-primary hover:bg-primary hover:text-white">
            Descargar Certificado
          </button>

          <div className="flex justify-center gap-4 mt-2">
            <Image src="/assets/icons/whatsapp.svg" alt="WhatsApp" width={24} height={24} />
            <Image src="/assets/icons/facebook.svg" alt="Facebook" width={24} height={24} />
            <Image src="/assets/icons/linkedin.svg" alt="LinkedIn" width={24} height={24} />
          </div>
        </div>
      )}
    </div>
  );
}
